@import "../Styles/variables.scss";

.single-animal-card-container {
  width: 100%;
  display: flex;
  justify-content: center;
}

.single-animal-card-wrapper {
  width: 50rem;
  height: 50rem;
  margin: 10px;
  border: 2px solid $subheading;
  background-color: $navbar;
  border-radius: 8px;
  box-shadow: 10px 5px 5px rgb(94, 78, 69);
}

.single-animal-container1 {
  display: flex;
  height: 25rem;
  width: auto;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: left;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
  padding: 1rem;
  margin: 5px 5px 0 5px;
  background-color: $header;
}

.image {
  max-width: 20rem;
  max-height: 24rem;
  height: auto;
  margin: 5px;
  border-radius: 4px;
}

.single-animal-description-text {
  font-size: 1rem;
  font-family: $font_1;
  margin: 10px;
}

%commonStyleContainer {
  height: 24rem;
  display: flex;
  flex-direction: column;
  justify-items: flex-start;
}

.single-animal-image-container,
.single-animal-text-container {
  @extend %commonStyleContainer;
}

a {
  color: $subheading;
  font-family: $font_1;
  font-size: 1rem;
}

.single-animal-container2 {
  display: flex;
  height: 20rem;
  width: auto;
  flex-direction: row;
  justify-content: flex-start;
  text-align: left;
  border-bottom-left-radius: 7px;
  border-bottom-right-radius: 7px;
  padding: 1rem;
  margin: 0px 5px 5px 5px;
  background-color: $apptitle;
}

.single-animal-rating-container {
  width: 24rem;
}

.star-ratings {
  margin: 5px;
  padding: 0.5rem;
  font-size: 1rem;
  font-family: $font_1;
}

.single-dog-container2 {
  display: flex;
  height: 20rem;
  width: auto;
  flex-direction: column;
  justify-content: flex-start;
  align-items: left;
  text-align: left;
  border-bottom-left-radius: 7px;
  border-bottom-right-radius: 7px;
  padding: 1rem;
  margin: 0 5px 5px 5px;
  background-color: $apptitle;
  font-size: 1rem;
  font-family: $font_1;
}


@media only screen and (max-width: 34rem) {
  .single-animal-card-wrapper {
    width: 18rem;
    height: auto;
  }

  .single-animal-container1 {
    flex-direction: column;
    height: auto;
  }
  .image {
    max-width: 10rem;
    max-height: 12rem;
    height: auto;
  }
  %commonStyleContainer {
    height: auto;
  }
  .single-animal-container2 {
    flex-direction: column;
    height: auto;
  }
  .single-dog-container2 {
    flex-direction: column;
    height: auto;
  }

  .star-ratings {
    display: flex;
    flex-direction: column;
    width: auto;
  }
  .single-animal-rating-container {
    width: auto;
  }
}

